<h1 id="在框架中使用" tabindex="-1"><a class="header-anchor" href="#在框架中使用" aria-hidden="true">#</a> 在框架中使用</h1>
<p>EasyWeChat 是一个通用的 Composer 包，所以不需要对框架单独做修改，只要支持 Composer 就能直接使用，当然了，为了更方便的使用，我们收集了以下框架单独提供的拓展包：</p>
<h2 id="laravel" tabindex="-1"><a class="header-anchor" href="#laravel" aria-hidden="true">#</a> Laravel</h2>
<blockquote>
<ul>
<li><a href="https://github.com/overtrue/laravel-wechat" target="_blank" rel="noopener noreferrer">overtrue/laravel-wechat<OutboundLink/></a></li>
</ul>
</blockquote>
<h2 id="symfony" tabindex="-1"><a class="header-anchor" href="#symfony" aria-hidden="true">#</a> Symfony</h2>
<blockquote>
<ul>
<li><a href="https://github.com/lilocon/WechatBundle" target="_blank" rel="noopener noreferrer">lilocon/WechatBundle<OutboundLink/></a></li>
</ul>
</blockquote>
<h2 id="yii" tabindex="-1"><a class="header-anchor" href="#yii" aria-hidden="true">#</a> Yii</h2>
<blockquote>
<ul>
