<li><a href="https://github.com/max-wen/yii2-easy-wechat" target="_blank" rel="noopener noreferrer">max-wen/yii2-easy-wechat<OutboundLink/></a> 适用于 EasyWeChat 3.x</li>
</ul>
</blockquote>
